@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,800&display=swap);
body {
  margin: 0;
  background: rgb(255,255,255);
  background: radial-gradient(circle, rgba(255,255,255,1) 45%, rgba(177,177,177,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff",endColorstr="#b1b1b1",GradientType=1);
  font-family: 'Open Sans', sans-serif;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  width: 100%;
}

.bg-dark {
  background-color: transparent !important;
}

.navbar-light .navbar-nav .nav-link {
  color: #ffffff !important;
  font-size: 0.6em;
  -webkit-transition: all 1s;
  transition: all 1s;
  text-transform: uppercase;
}

.navbar-light .navbar-nav .nav-link:hover {
  background: #006380;
  color: #ffffff !important;
}

.form-control {
  font-size: 0.7em !important;
  background-color: rgba(255,255,255,0.7) !important;
}

.btn {
  font-size: 0.7em !important;
}
.Footer {
  width: 100%;
  text-align: center;
}

.Footer h2 {
  font-weight: 100;
  font-size: 0.7em;
  padding: 10px;
  color: #ffffff;
}
.Home {
  display: flex;
  height:80vh;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.Contenidos {
  flex: 1 1;
  text-align: center;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.Home h2 {
  font-weight: 100;
  color: #ffffff;
  font-size: 2em;
}

.Home h3 {
  font-weight: 100;
  color: #ffffff;
  font-size: 1.3em;
}

.Home p {
  font-weight: 100;
  color: #ffffff;
  font-size: 0.8em;
}
.ItemCarousel {
  background: rgba(0,0,0,0.3);
  padding: 10px;
  display: flex;
  flex-direction: row;
  -webkit-transition: all 1s;
  transition: all 1s;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.ItemCarousel:hover {
  background: rgba(0,0,0,0.8);
}

i {
  color: #ffffff;
  font-size: 1.3em;
}

.ItemCarousel h2 {
  font-size: 1.3em;
  text-align: left;
  padding-left: 10px;
  cursor: pointer;
  padding-bottom: 0;
  margin-bottom: 0;
  margin-top: 0;
  padding-top: 0;
}

.ItemCarousel p {
  font-size: 0.7em;
  color: #ffffff;
  text-align:left;
  padding-left: 10px;
}
.carousel {
  position: fixed !important;
  width: 100%;
  z-index: -1;
  top: 0;
}

.carousel-caption {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  justify-content: center;
}

.introx {
  background: rgb(48,82,121);
  background: radial-gradient(circle, rgba(48,82,121,1) 45%, rgba(0,0,0,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#305279",endColorstr="#000000",GradientType=1);
  display: flex !important;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}
.Site {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Site-content {
  flex: 1 1;
  width: 100%;
}

.form-control {
  border-radius: 0 !important;
}

.btn {
  border-radius: 0 !important;
}
